import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CalzadoComponent } from './calzado.component';
import { DetailComponent } from '../shared/detail.component/detail.component.component';

@NgModule({
  imports: [
    RouterModule.forChild([
      { path: 'calzado', component: CalzadoComponent },
      { path: 'calzado/producto/:id', component: DetailComponent }
    ])
  ],
  exports: [RouterModule]
})
export class CalzadoRoutingModule { }



