import { Component, OnInit } from '@angular/core';
import { CatalogoServiceService } from '../shared/catalogo-service.service';
import { Observable } from 'rxjs/Observable';
import { ItemCatalogo } from '../shared/modelo/item-catalogo';

@Component({
  selector: 'app-calzado',
  templateUrl: './calzado.component.html',
  styleUrls: ['./calzado.component.css']
})
export class CalzadoComponent implements OnInit {

  catalogo: Observable<ItemCatalogo[]>;


  constructor(public catalogoServiceService: CatalogoServiceService) { }

  ngOnInit() {
    this.catalogo = this.catalogoServiceService.getCalzado();
  }

}



