import { BrowserModule } from '@angular/platform-browser';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { PolymerModule } from '@codebakery/origami';
import { AppElementsModule, IronElementsModule, PaperElementsModule } from '@codebakery/origami/lib/collections'; // O

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';


import { InicioModule } from './inicio/inicio.module';
import { CatalogoModule } from './catalogo/catalogo.module';
import { ContactenosModule } from './contactenos/contactenos.module';
import { QuienesSomosModule } from './quienes-somos/quienes-somos.module';
import { SharedModule } from './shared/shared.module';
import { BilleterasModule } from './billeteras/billeteras.module';
import { CalzadoModule } from './calzado/calzado.module';


@NgModule({
  declarations: [
    AppComponent
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  imports: [
    FormsModule, // Required to connect elements to Angular forms
    PolymerModule.forRoot(), // Only import .forRoot() once and at the highest level

    // Optional modules to help reduce markup complexity
    IronElementsModule,
    PaperElementsModule,
    BrowserModule,
    InicioModule,
    CatalogoModule,
    ContactenosModule,
    QuienesSomosModule,
    AppRoutingModule,
    BilleterasModule,
    CalzadoModule,
    SharedModule.forRoot()
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



