import { Router, ActivatedRoute, Params } from '@angular/router';
import { OnInit, Component } from '@angular/core';
import { CatalogoServiceService } from '../catalogo-service.service';
import { ItemCatalogo } from '../modelo/item-catalogo';
import { Observable } from 'rxjs/Observable';
import { ICarouselConfig, AnimationConfig } from 'angular4-carousel';

@Component({
  selector: 'app-detail.component',
  templateUrl: './detail.component.component.html',
  styleUrls: ['./detail.component.component.css']
})
export class DetailComponent implements OnInit {

  producto: string;
  catalogo: Observable<ItemCatalogo[]>;

  public config: ICarouselConfig = {
    verifyBeforeLoad: true,
    log: false,
    animation: true,
    animationType: AnimationConfig.SLIDE,
    autoplay: false,
    autoplayDelay: 2000,
    stopAutoplayMinWidth: 768
  };

  constructor(private activatedRoute: ActivatedRoute,
    public catalogoServiceService: CatalogoServiceService) { }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params: Params) => {
      this.producto = params['id'];
      console.log(this.producto);
      if (this.producto === 'calzado') {
        this.catalogo = this.catalogoServiceService.getCalzado();
      } else if (this.producto.startsWith('billete')) {
        this.catalogo = this.catalogoServiceService.getBilleteras();
      } else {
        this.catalogo = this.catalogoServiceService.get();
      }

    });

  }

  getImagen(id: string, index: string) {
    return 'assets/images/' + id + '/' + id + ' (' + index + ').jpg';
  }

  getImageSources(id: string, index: number) {
    const images = [];
    for (let _i = 1; _i <= index; _i++) {
      images.push('assets/images/' + id + '/' + id + ' (' + _i + ').jpg');
    }
    console.log(images);
    return images;
  }

}



