import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { CatalogoServiceService } from '../shared/catalogo-service.service';
import { ItemCatalogo } from '../shared/modelo/item-catalogo';

@Component({
  selector: 'app-billeteras',
  templateUrl: './billeteras.component.html',
  styleUrls: ['./billeteras.component.css']
})
export class BilleterasComponent implements OnInit {

  catalogo: Observable<ItemCatalogo[]>;


  constructor(public catalogoServiceService: CatalogoServiceService) { }

  ngOnInit() {
    this.catalogo = this.catalogoServiceService.getBilleteras();
  }

}



