import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { BilleterasComponent } from './billeteras.component';
import { DetailComponent } from '../shared/detail.component/detail.component.component';

@NgModule({
  imports: [
    RouterModule.forChild([
      { path: 'billeteras', component: BilleterasComponent },
      { path: 'billeteras/producto/:id', component: DetailComponent }
    ])
  ],
  exports: [RouterModule]
})
export class BilleterasRoutingModule { }



