import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Http, URLSearchParams, Headers, RequestOptions, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { ItemCatalogo } from '../shared/modelo/item-catalogo';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';

@Injectable()
export class CatalogoServiceService {

  constructor(private http: Http) { }

  getCalzado(): Observable<ItemCatalogo[]> {
    return this.http.get('assets/calzado.json').map((res) => res.json())
       .catch(this.handleError);
  }

  getBilleteras(): Observable<ItemCatalogo[]> {
    return this.http.get('assets/billeteras.json').map((res) => res.json())
       .catch(this.handleError);
  }

  get(): Observable<ItemCatalogo[]> {
    return this.http.get('assets/data.json').map((res) => res.json())
       .catch(this.handleError);
  }


  private handleError(error: any) {
    const errMsg = (error.message) ? error.message :
      error.status ? `${error.status} - ${error.statusText}` : 'Server error';
    console.error(errMsg);
    return Observable.throw(errMsg);
  }

}



