import { Component, OnInit } from '@angular/core';
import { CatalogoServiceService } from '../shared/catalogo-service.service';
import { ItemCatalogo } from '../shared/modelo/item-catalogo';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-catalogo',
  templateUrl: './catalogo.component.html',
  styleUrls: ['./catalogo.component.css']
})
export class CatalogoComponent implements OnInit {
  catalogo: Observable<ItemCatalogo[]>;
  constructor(public catalogoServiceService: CatalogoServiceService) { }

  ngOnInit() {
    this.catalogo = this.catalogoServiceService.get();
  }

  getImagen(item: ItemCatalogo) {
    return 'assets/images/' + item.id + '/' + item.id + ' (1).jpg';
  }

  getRoute(producto: string) {
    return 'producto/' + producto;
  }

}



