import { NgModule, ModuleWithProviders, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { HttpModule } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { CarouselModule } from 'angular4-carousel';
import { DetailComponent } from './detail.component/detail.component.component';
import { PolymerModule } from '@codebakery/origami';
import { CatalogoServiceService } from './catalogo-service.service';
import {
  AppElementsModule,
  IronElementsModule,
  PaperElementsModule
} from '@codebakery/origami/collections';
import { GridItemsComponent } from './grid-items/grid-items.component';
/**
 * Do not specify providers for modules that might be imported by a lazy loaded module.
 */

@NgModule({
  imports: [
    AppElementsModule,
    BrowserModule,
    CommonModule,
    FormsModule,
    HttpModule,
    IronElementsModule,
    PaperElementsModule,
    PolymerModule.forRoot(),
    RouterModule,
    HttpClientModule, CarouselModule
  ],
  providers: [CatalogoServiceService],
  declarations: [DetailComponent, GridItemsComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  exports: [CommonModule, FormsModule, RouterModule, HttpClientModule, HttpModule, CarouselModule, GridItemsComponent]
})
export class SharedModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedModule,
      providers: []
    };
  }
}



