import { Component, OnInit, Input } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { ItemCatalogo } from '../modelo/item-catalogo';


@Component({
  selector: 'app-grid-items',
  templateUrl: './grid-items.component.html',
  styleUrls: ['./grid-items.component.css']
})
export class GridItemsComponent implements OnInit {

  @Input()  items: ItemCatalogo[] ;

  constructor() { }

  ngOnInit() {
  }

  getImagen(item: ItemCatalogo) {
    return 'assets/images/' + item.id + '/' + item.id + ' (1).jpg';
  }

  getRoute(producto: string) {
    return 'producto/' + producto;
  }

}



